   }

   .control-element :global(.number-container > .button-small:hover > svg > polygon) {
      fill: var(--main-color1, #6eb8ff);
   }

   .control-element :global(.number-container:focus-visible) {
      outline-color: var(--outline-color, #ccc);
   }

</style>
