import {Select, RangeDiscrete, Switch} from 'svelte-controls-basic';

export function getPlotBiControls(res) {

   const resIDs = Object.keys(res);
   const resobj = res[resIDs[0]];

   const controls = {
      resID: { el: Select, hidden: resIDs.length == 1, default: resIDs[0], label: 'Show', props: {options: resIDs}},
      comp2: { el: RangeDiscrete, hidden: false, default: resobj.ncomp > 1 ? 2 : 1, label: 'PC (y-axis)', props: {min: 1, max: resobj.ncomp}},
      comp1: { el: RangeDiscrete, hidden: false, default: 1, label: 'PC (x-axis)', props: {min: 1, max: resobj.ncomp}},
      showLabels: { el: Select, hidden: false, default: 'hide', label: 'Labels', props: {options: ['hide', 'obj', 'var', 'both']}},
      axesEqual: { el: Switch, hidden: false, default: false, label: 'Axes', props: {options: ['normal', 'equal']}},
   };

   return controls;
}
