import { Vector } from 'mdatools/arrays';
import { qchisq } from 'mdatools/distributions';
import { getAxisTicks, getcolmap } from 'svelte-plots-basic/utils';

// default plot settings
export const MARGINS = [1.0, 1.1, 0.45, 0.4];

// default colors for SIMCA categories
export const CAT_COLORS = {
   'regular': '#2679B2',
   'extreme': '#F2B825',
   'outlier': '#D22C2F', 'outliers': '#D22C2F',
   'alien': '#2679B2', 'aliens': '#2679B2',
   'external': '#D22C2F'
};

// default colors for series
export const LINE_COLORS = {'cal': '#2679B2', 'pv': '#D22C2F', 'target': '#2679B2', 'alt': '#2679B2', 'new': '#2679B2'};
export const FACE_COLORS = {'cal': '#f0f8ff', 'pv': '#fff8f0', 'target': '#f0f8ff', 'alt': '#f0f8ff', 'new': '#f0f8ff'};
export const LABELS_COLOR = '#a0a0a0';
export const LABELS_COLOR_SELECTED = '#909090';
export const AXIS_COLOR = '#909090';
export const MAX_LABEL_SYMBOLS = 8;

// default parameters to save plots as PNG file
export const PNG_WIDTH = 8;   // cm
export const PNG_HEIGHT = 8;  // cm
export const PNG_RES = 600;   // pixels per inch


/**
 * Generates axis title for given component and result object.
 * @param resobj - result object (single)
 * @param ncomp - component index (from 1)
 */
export function getAxisTitle(resobj, ncomp) {
   if (!resobj) return '';
   if (!ncomp || ncomp < 0 || ncomp > resobj.ncomp) return '';
   return `PC${ncomp} (${(resobj.stat.expvar[ncomp - 1] * 100).toFixed(1)}%)`;
}



/**
 * Take a subset of values without outliers.
 * @param {TypedArray} x - vector with selected variable values.
 * @param {Array} samples - row indices of samples without excluded outliers
 */
export function subset(x, samples) {
   if (x.length === samples.length) return x;
   const n = samples.length;
   const out = new x.constructor(n);
   for (let i in samples) {
      out[i] = x[samples[i]];
   }
   return out;
}


/**
 * Splits data values into n intervals and return a vector with interval boundaries.
 * @param {TypedArray} x -
 * @param n
 */
export function split(x, n) {
   let xMax = Number.NEGATIVE_INFINITY;
   let xMin = Number.POSITIVE_INFINITY;

   // compute min and max
   for (let i = 0; i < x.length; i++) {
      xMin = Math.min(xMin, x[i]);
      xMax = Math.max(xMax, x[i]);
   }

   // get nice values for colorbar ticks
   let ticks = getAxisTicks(undefined, [xMin, xMax], n - 1, true, false, 0).v;

   // add extra on left and right if ticks do not cover the whol range
   const dt = ticks[1] - ticks[0];
   if (ticks[0] > xMin) {
      const left = ticks[0] - dt;
      ticks = [left < xMin ? left : xMin, ...ticks];
   }
   if (ticks[ticks.length - 1] < xMax) {
      const right = ticks[ticks.length - 1] + dt;
      ticks = [...ticks, right > xMax ? right : xMax];
   }

   return ticks;
}


/**
 * Returns JSON with color for each class category
 * @param {Array} classNames - array with unique class names
 * @returns {Object}
 */
export function getClassColors(classNames) {
   const n = classNames.length;
   if (n < 1 || n > 16) {
      throw Error("getClassColors: can not create color map for more than 16 categories.");
   }

   const colors = getcolmap(n);
   const out = classNames.reduce((acc, curr, index) => {
      acc[curr] = colors[index];
      return acc;
   }, {});
   return out;
}


/**
 * Generates a vector of colors for each value in vector "x" depending on which interval its value falls into.
 * @param {TypedArray} x - vector with variable values (outliers free).
 * @param {Array} breaks - array with boundaries of the intervals.
 * @param {Array} colmap - array with color for each interval.
 *
 * @returns {Array} - same size as x, but with colors.
 */
export function getObjColors(x, breaks, colmap) {
   const n = breaks.length;

   // add a bit extra to right side of the last bin
   const colors = new Array(x.length);
   const delta = breaks[1] - breaks[0];
   // process the values
   for (let i = 0; i < x.length; i++) {
      for (let j = 0; j < n - 1; j++) {
         const left = x[i] >= breaks[j];
         const right = j === n - 2 ? x[i] < breaks[j + 1] + delta: x[i] < breaks[j + 1];
         if (left && right) {
            colors[i] = colmap[j];
            break;
         }
      }
   }

   return colors;
}


export function getColorGroups(dataset, resobj, colorBy, colorByVar, resID = 'cal') {

   if (!resobj) return null;

   let labels, map, breaks, colors, varname;
   if (colorBy === 'groups' && resobj.classNames) {
      // color by groups
      const classColmap = getClassColors(resobj.classNames);
      map = Object.keys(classColmap).map(v => classColmap[v]);
      labels = resobj.classNames;
      colors = resobj.classes.map(v => classColmap[v]);
      breaks = new Array(labels.length + 1).map((v, i) => i - 0.5);
      varname = null;
   } else if (colorBy === 'variable') {
      // color by variable
      const colorVar = subset(dataset.values.getcolref(colorByVar), resobj.objindices);
      varname = dataset.varlabels[colorByVar - 1];
      if (varname.length > 30) {
         varname = varname.substring(0, 29) + '...'; // limit varname by 30-32 symbols to fit the plot width
      }
      breaks = split(colorVar, 10);
      map = getcolmap(breaks.length - 1);
      colors = getObjColors(colorVar, breaks, map);
   } else {
      // no color grouping
      labels = null;
      breaks = null;
      map = null;
      colors = Array(resobj.nrows).fill(LINE_COLORS[resID]);
   }

   return {labels, map, breaks, colors, varname};
}

/**
 * Returns JSON with x and y coordinates, legend item, colors and axis limits for a particular
 * statistic (e.g. sensitivity, specificity, etc) of result object.
 *
 * @param {Object} resobj - JSON with one or several result objects (SIMCA, PCA, etc.).
 * @param {string} id - ID of the object to compute the series values for (e.g. "cal" or "pv").
 * @param {string} statName - name of statistic to extract (e.g. "sens").
 * @returns {Object}
 */
export function getSeries(resobj, id, statName) {

   if (!resobj || !id) return null;

   if (!Object.keys(resobj).includes(id)) {
      throw Error('getSeries: the result object has no element with ID: "' + id + '"');
   }

   const res = resobj[id];

   const ncomp = res.v.length;
   const y = Array(ncomp);
   const x = Array(ncomp);
   let yMin = 9999999999;
   let yMax = -999999999;

   for (let a = 0; a < ncomp; a++) {
      const ya = res.v[a].stat[statName];
      x[a] = a + 1;
      y[a] = ya;
      yMin = Math.min(yMin, ya);
      yMax = Math.max(yMax, ya);
   }

   const legendItem = {label: id, marker: 1, borderColor: LINE_COLORS[id], faceColor: FACE_COLORS[id]};
   const lineColor = LINE_COLORS[id];
   const faceColor = FACE_COLORS[id];
   return {x, y, legendItem, lineColor, faceColor, yMin, yMax};
}



/**
 * Computes coordinates of confidence ellipse ellipse.
 *
 * @param {Array} covMat - variance-covariance matrix for coordinates (xx, xy, yy).
 * @param {Array} center - coordinates of the center (x, y).
 * @param {Number} confLevel - confidence level (between 0 and 1).
 *
 * @returns {Array} array with six elements: x- and y-coordinates of the confidence ellipse
 * points (Vectors), smallest and largest values for each coordinates.
 */
export function getEllipse(covMat, center, confLevel = 0.95) {

   // define number of ellipse points and angle increments
   const nTheta = 100;
   const dTheta = 2 * Math.PI / (nTheta - 1);

   // compute eigenvalues
   const [a, b, d] = covMat;
   const D = Math.sqrt((a + d)**2 - 4 * (a * d - b * b));
   let l1 = 0.5 * (a + d - D);
   let l2 = 0.5 * (a + d + D);

   // swap eignevalues if needed so l1 ≥ l2
   if (l1 < l2) {
      let l = l1;
      l1 = l2;
      l2 = l;
   }

   // compute radii of the future ellipse
   const chisq = qchisq(confLevel, 2);
   const r1 = Math.sqrt(chisq * l1);
   const r2 = Math.sqrt(chisq * l2);

   let v1x, v1y, phi;
   if (Math.abs(b) < 10e-8) {
      v1x = 1;
      v1y = 0;
      phi = 0;
   } else {
      // compute largest eigenvactor and normalized it
      let v1x = -b / (a - l1);
      let v1y = 1;
      const n1 = Math.sqrt(v1x * v1x + v1y * v1y);
      v1x = v1x / n1;
      v1y = v1y / n1;
      phi = Math.atan2(v1y, v1x);
      if (phi < 0) phi = phi + 2 * Math.PI;
   }

   // get angle between the x-axis and the largest eigenvector

   // pre-compute cos and sin of the angle
   const cosphi = Math.cos(phi);
   const sinphi = Math.sin(phi);

   // prepare vectors for coordinates of the ellipse points
   const xVec = Vector.zeros(nTheta);
   const yVec = Vector.zeros(nTheta);

   // reset min/max values
   let xMin = Number.POSITIVE_INFINITY;
   let xMax = Number.NEGATIVE_INFINITY;
   let yMin = Number.POSITIVE_INFINITY;
   let yMax = Number.NEGATIVE_INFINITY;

   // compute and save coordinates of the ellipse points
   for (let i = 0; i < nTheta; i++) {
      const theta = i * dTheta;
      const x = r1 * Math.cos(theta);
      const y = r2 * Math.sin(theta);
      const xv = x * cosphi - y * sinphi + center[0];
      const yv = x * sinphi + y * cosphi + center[1];
      xVec.v[i] = xv;
      yVec.v[i] = yv;
      xMin = Math.min(xMin, xv);
      xMax = Math.max(xMax, xv);
      yMin = Math.min(yMin, yv);
      yMax = Math.max(yMax, yv);
   }

   return [xVec, yVec, xMin, xMax, yMin, yMax];
}


/**
 * Computes coordinates of confidence ellipses for each group of points.
 *
 * @param {Vector} xValues - x-coordinates of points (on scatter plot).
 * @param {Vector} yValues - y-coordinates of the points.
 * @param {Array} groups - array with group label for each point.
 * @param {Array} groupNames - array with unique group labels.
 * @param {Array} groupColors - array with color for each group (same as used for the points).
 * @param {Number} confLevel - confidence level (between 0 and 1).
 *
 * @returns {JSON} JSON containing coordinates and colors of the ellipses as well as
 * four values - min and max of x- and y-coordinates of the ellipses.
 */
export function getEllipses(xValues, yValues, groups, groupNames, groupColors, confLevel) {

   // check inputs
   if (!xValues || !yValues || !groups || !groupNames || !groupColors) return null;
   const n = xValues.length;
   const nGroups = groupNames.length;

   // for each group compute sum of coordinates and number of points
   const numPoints = new Array(nGroups).fill(0);   // number of point in each group
   const centers = new Array(nGroups * 2).fill(0); // 2 x nGroups - each column is a group

   for (let i = 0; i < n; i++) {

      // index of group the value belongs to
      const ind = groupNames.indexOf(groups[i])

      // number of points in and sum of coordinates in this group
      numPoints[ind] += 1;
      centers[ind * 2] += xValues.v[i];
      centers[ind * 2 + 1] += yValues.v[i];
   }

   // divide sum of coordinates to the number of groups to get centers
   for (let i = 0; i < nGroups; i++) {
      centers[i * 2] = centers[i * 2] / numPoints[i];
      centers[i * 2 + 1] = centers[i * 2 + 1] / numPoints[i];
   }

   // compute elements of variance-coveriance matrix
   const covMat = new Array(nGroups * 3).fill(0); // 3 x nGroups - each column is a group
   for (let i = 0; i < n; i++) {

      // index of group the value belongs to
      const ind = groupNames.indexOf(groups[i])

      // elements of variance/covariance matrix (xx, xy, yy)

      const x = (xValues.v[i] - centers[ind * 2]);
      const y = (yValues.v[i] - centers[ind * 2 + 1]);
      covMat[ind * 3]     += x * x;
      covMat[ind * 3 + 1] += x * y;
      covMat[ind * 3 + 2] += y * y;
   }

   // reset min/max values
   let minXVal = Number.POSITIVE_INFINITY;
   let maxXVal = Number.NEGATIVE_INFINITY;
   let minYVal = Number.POSITIVE_INFINITY;
   let maxYVal = Number.NEGATIVE_INFINITY;

   // divide sum of coordinates to the number of groups to get centers and compute coordinates of ellipses
   const ellipses = Array(nGroups);
   for (let i = 0; i < nGroups; i++) {
      const [xE, yE, xMin, xMax, yMin, yMax] = getEllipse(
         [covMat[i * 3] / (numPoints[i] - 1), covMat[i * 3 + 1] / (numPoints[i] - 1), covMat[i * 3 + 2] / (numPoints[i] - 1)],
         [centers[i * 2], centers[i * 2 + 1]],
         confLevel
      )
      ellipses[i] = {x: xE, y: yE, c: groupColors[i]};
      minXVal = Math.min(minXVal, xMin);
      maxXVal = Math.max(maxXVal, xMax);
      minYVal = Math.min(minYVal, yMin);
      maxYVal = Math.max(maxYVal, yMax);
   }

   return {ellipses, xRange: [minXVal, maxXVal], yRange: [minYVal, maxYVal]};
}
