import {Select, PlotTypeSelector, Switch} from 'svelte-controls-basic';

export function getPlotVarianceControls(res) {

   const resIDs = Object.keys(res);
   const resobj = res[resIDs[0]];

   const controls = {
      plotType: { el: PlotTypeSelector, hidden: false, default: 'b', label: 'Plot type', props: {options: ['b', 'h']}},
      resID: { el: Select, hidden: resIDs.length == 1, default: resIDs[0], label: 'Show', props: {options: resIDs}},
      varType: { el: Select, hidden: false, default: 'explained', label: 'Type', props: {options: ['explained', 'residual']}},
      varForm: { el: Select, hidden: false, default: 'cumulative', label: 'Form', props: {options: ['individual', 'cumulative']}},
      showLabels: { el: Switch, hidden: false, default: false, label: 'Show labels', props: {options: ['hide', 'show']}},
   };

   return controls;
}
