import {PlotTypeSelector, RangeDiscrete, Switch} from 'svelte-controls-basic';

export function getPlotLoadingsControls(model) {

   const controls = {
      plotType: { el: PlotTypeSelector, hidden: false, default: 'p', label: 'Plot type', props: {options: ['p', 'l', 'h']}},
      comp2: { el: RangeDiscrete, hidden: false, default: model.ncomp > 1 ? 2 : 1, label: 'PC (y-axis)', props: {min: 1, max: model.ncomp}},
      comp1: { el: RangeDiscrete, hidden: false, default: 1, label: 'PC (x-axis)', props: {min: 1, max: model.ncomp}},
      showLabels: { el: Switch, hidden: false, default: false, label: 'Show labels', props: {options: ['hide', 'show']}},
      axesEqual: { el: Switch, hidden: false, default: false, label: 'Axes', props: {options: ['normal', 'equal']}},
   };

   return controls;
}
