<script>
   /*************************************************************/
   /* PlotVariance                                              */
   /* --------------------                                      */
   /* shows plot with variance values vs. number of PCs         */
   /*************************************************************/

   import { XAxis, YAxis, Box } from 'svelte-plots-basic/2d';
   import { vector } from 'mdatools/arrays';
   import { FACE_COLORS } from './Defaults.js';

   import PlotSeries from './PlotSeries.svelte';
   import Axes from './Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();

   // get the parameters needed for the plot
   let res = $derived(props ? props.res : null);
   let resobj = $derived(res ? res[params.resID] : null);


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const limY = [-0.05, 1.1];
   const titles = {
      'expvar': 'Explained variance',
      'resvar': 'Residual variance',
      'cumexpvar': 'Cumulative explained variance',
      'cumresvar': 'Cumulative residual variance'
   };

   let varForm = $derived(params.varForm === 'cumulative' ? 'cum' : '');
   let varType = $derived(params.varType === 'explained' ? 'exp' : 'res');
   let statName = $derived(varForm + varType + 'var');
   let title = $derived(titles[statName]);

   let limX = $derived(resobj ? [1 - resobj.ncomp * 0.05, resobj.ncomp + resobj.ncomp * 0.05] : [0, 1]);
   let xValues = $derived(resobj ? resobj.stat.components : null);
   let yValues = $derived(resobj ? vector(resobj.stat[statName]) : null);
   let labels = $derived(yValues ? Array.from(yValues.v).map(v => v.toFixed(v < 0.1 ? 3 : 2)) : null);
</script>

{#if xValues && yValues }
<Axes {title} {limX} {limY} fileName={'plot-' + statName} >

   <!-- data points  -->
   <PlotSeries
      {xValues}
      {yValues}
      {labels}
      plotType={params.plotType}
      showLabels={params.showLabels}
      faceColor={FACE_COLORS['cal']}
      lineWidth={1.5}
   />

   <XAxis showGrid={true} label='Number of PCs' whole={true} />
   <YAxis showGrid={true} label='Variance' />
   <Box />
</Axes>
{/if}